export default {
	data() {
		return {
			message: '',
			baseUrl: '',
			userToken: false
		};
	},
	onLoad() {
		let token = uni.getStorageSync('storage_token');
		if (token == '' || token == null || token == undefined) {
			this.userToken = false;
		} else {
			this.userToken = true;
		}
	},
	onShow() {
		uni.setNavigationBarTitle({
				title: this.$t('index.zhengce')
		})	
		
		this.baseUrl = this.websiteUrl;
		let token = uni.getStorageSync('storage_token');
		console.log(this.baseUrl);
		console.log(token);
		uni.request({
			url: this.baseUrl + '/api/config',
			method: 'get',
			data: {
				type: 'privacy'
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				token: token
			},
			success: res => {
				console.log(res.data.data);
				// this.message = res.data.data;
				let remarkStr = '';
				// remarkStr = res.data.data.replace(/\n/g,'<view></view>')
				const system_info = uni.getStorageSync('system_info');
				console.log(system_info.language);
				if(system_info.language == "zh_CN" || system_info.language == "zh-CN"){
					remarkStr = res.data.data.cn.replace('\g','')
					this.message = remarkStr;
				}else {
					remarkStr = res.data.data.en.replace('\g','')
					this.message = remarkStr;
				}
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		go_login() {
			uni.navigateTo({
				url: '../login/login'
			});
		}
	}
};
